#works {
  min-height: calc(100vh - 4rem);
  width: 100vw;
  max-width: 60rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  text-align: center;
  padding: 0 1rem;
}

.worksTitle {
  margin: 1rem 0;
  font-size: 3rem;
}

.worksDes {
  font-weight: 300;
  font-size: 1rem;
  max-width: 45rem;
}

.worksImgs {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width: 80vw;
  max-width: 65rem;
}

.worksImg {
  object-fit: cover;
  height: 10rem;
  background-color: #d3e4cd;
  border-radius: 11px;
  padding: 20px;
  margin: 10px;
}

@media screen and (max-width: 585px) {
  .worksImg {
    height: 48vw;
  }
}
